.History {
  margin-top: 2rem;
}

.History__title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 2rem;
}
  .History__title h2 {
    margin: 0;
    opacity: 0.5;
  }

.History__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.History__list-item {
  padding: 0.5rem 0;
}

.History__list-item .HistoryItem {  
  -webkit-transition: opacity 0.2s ease-in, font-size 0.2s ease-in;  
  -o-transition: opacity 0.2s ease-in, font-size 0.2s ease-in;  
  transition: opacity 0.2s ease-in, font-size 0.2s ease-in;
}
  .History__list-item:hover .HistoryItem {
    opacity: 1 !important;
  }

  .History__list-item:nth-child(1) .HistoryItem {
    font-size: 1.1em;
  }
  .History__list-item:nth-child(2) .HistoryItem {
    opacity: 0.8;
  }
  .History__list-item:nth-child(3) .HistoryItem {
    opacity: 0.6;
  }
  .History__list-item:nth-child(4) .HistoryItem {
    opacity: 0.4;
  }
  .History__list-item:nth-child(1n+5) .HistoryItem {
    opacity: 0.2;
  }
  .History__list-item:nth-child(1n+8) .HistoryItem {
    opacity: 0.1;
  }


