.BitDisplay {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.BitDisplay__bits {
  display: -ms-flexbox;
  display: flex;
}
  .BitDisplay__bits .Bit:nth-child(4n) {
    margin-right: 1em;
  }

  .BitDisplay__bits .Bit:nth-child(1) {
    margin-left: 0;
  }

  .BitDisplay__bits .Bit:nth-child(16) {
    margin-right: 0;
  }

.BitDisplay__value {
  margin-left: 2rem;
  font-size: 1.2em;
  width: 4em;
  text-align: right;
}


